<template>
  <div class="wrap">
    <div class="top-bar">
      <p>{{item.title}}</p>
      <div class="search">扫一扫</div>
    </div>
    <ul v-infinite-scroll="playersMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" key="activity">
      <li v-for="(item, i) in players" :key="i">
        <player-item :item="item">
        </player-item>
      </li>
    </ul>
  </div>
</template>
<script>
import Item from '../components/player-item';
import api from '../api/mgr';

export default {
  name: 'mgr-login',
  components: { Item },
  data() {
    return {
      page: 1,
      players: [],
      keyword: '',
      id: '',
      item: '',
    };
  },
  created() {
    this.id = this.$route.params.id;
  },
  mounted() {
    this.handleDetail();
  },
  methods: {
    async handleDetail() {
      const res = await api.getActivityDetail(this.id);
      this.item = res.data;
    },
    async handleAction() {
      const res = await api.getPlayers(this.page, this.id);
      this.page = this.page + 1;
      this.players = res.data.list;
    },
    playersMore() {
      this.handleAction();
    },
  },
};
</script>
<style lang="scss" scoped>
.wrap {
  position: absolute;
  min-height: 100%;
  width: 100%;
  padding: 10px 30px 0;
  background: #f2f2f2;
}
.top-bar {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  p {
    font-weight: bold;
    color: #333;
    font-size: 20px;
  }
  .search {
    padding: 3px 10px;
    border-radius: 5px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
  }
}
.form {
  display: block;
  width: 100%;

  .item {
    display: block;
    margin-bottom: 10px;
    label {
      display: block;
      color: #666;
      margin-bottom: 3px;
    }
    input {
      display: block;
      width: 100%;
      border: 0;
      padding: 10px 5px;
      background: white;
      border-radius: 5px;
      outline-style: none;
    }
  }
}

button {
  width: 80%;
  margin: 50px 10%;
  background: #fdda06;
  padding: 15px;
  border: 0;
  border-radius: 5px;
  outline: none;
}
</style>





