import request from '../utils/request';

const login = data =>
  request({
    url: '/store/login/login',
    method: 'post',
    data,
  });
const getInfo = () =>
  request({
    url: '/store/store/detail',
    method: 'get',
  });
const getActivities = (page, title) =>
  request({
    url: '/store/activity/list',
    method: 'get',
    params: {
      page,
      title,
    },
  });
const getActivityDetail = id =>
  request({
    url: '/store/activity/detail',
    method: 'get',
    params: {
      id,
    },
  });
const getPlayers = (page, id) =>
  request({
    url: '/store/activity/join-list',
    method: 'get',
    params: {
      page,
      id,
    },
  });
export default {
  login,
  getInfo,
  getActivities,
  getActivityDetail,
  getPlayers,
};



