<template>
  <div class="item">
    <div class="head">
      <img :src="item.headimgurl" alt="">
    </div>
    <div class="message">
      <p>{{item.name}}</p>
      <p>{{item.phone}}</p>
    </div>
    <div class="status">
      {{item.status === 1 ? '已签到' : '未到位'}}
    </div>
  </div>
</template>
<script>
export default {
  name: 'player-item',
  props: {
    item: String,
  },
};
</script>
<style lang="scss" scoped>
.item {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 3px 5px;
  background: white;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
}
</style>





